import * as React from 'react';
import { useSearchParams } from 'react-router-dom';

import './forms.scss';

import { getCN } from '@miq/utiljs';
import { Inputs } from '@miq/formjs';

export type ProductSearchFormProps = {
  className?: string;
};

export const ProductSearchForm = (props: ProductSearchFormProps) => {
  const [search, setSearch] = useSearchParams();

  return (
    <form {...props} className={getCN(['p-search-form', props.className])}>
      <Inputs.SearchInput
        // required
        value={search.get('q') || ''}
        onChange={(e) => {
          setSearch({ q: e.target.value });
        }}
        placeholder="Cherchez un produit ..."
      />
    </form>
  );
};
